import React from 'react';
import {useEffect, useState} from 'react';
import {BrowserRouter as Router, Route, Switch, Link, withRouter} from "react-router-dom";
import Room from './Room'

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {key: 0};
        this.inputRef = React.createRef();
    }

    submitAction = () => {
        console.log('this is submitAction');
        let key = this.inputRef.current.value;
        this.props.history.push(`/roomescape/${key}`);
        window.location.reload();
    };

    componentDidMount() {

    }

    render() {
        return (
            <div>
                <input type={`text`} ref={this.inputRef}></input>
                <button onClick={this.submitAction}>Submit</button>
                <Router>
                    <Route exact path={`/roomescape`} component={home}/>
                    <Route path={`/roomescape/:key`} component={Room}/>
                </Router>
            </div>
        )
    }
}

let home = () => {
    return (
        <div>
            <h1>방탈출게임에 오신것을 환영합니다.</h1>
            <h2>'enter'을 입력하시고 첫번째 방으로 입장하세요</h2>
        </div>
    )
}


export default withRouter(App);
